    position: relative;
    height: 40px;
}

.root_box .line {
    border-bottom: 1px solid #e5e5e5;
}

.root_box .menu_box div {
    position: absolute;
    width: 100%;
    height: 40px;
    line-height: 20px;
    padding-top: 10px;
    padding-left: 10px;
    font-size: 13px;
    font-weight: 400;
    color: #555555;
    cursor: pointer;
}

